.gform_wrapper {
  position: relative;
}
.gform_wrapper .validation_error {
  font-size: 13px;
  color: #A9233E;
  text-align: center;
  text-transform: uppercase;
  padding: 10px 0;
  margin: 15px 0;
  border-top: 2px solid #A9233E;
  border-bottom: 2px solid #A9233E;
  font-weight: bold;
}
.gform_wrapper form .gform_body {
  width: 100%;
}
.gform_wrapper form .gform_body h2.gsection_title {
  font-size: 18px;
  margin: 25px 0 20px;
}
.gform_wrapper form .gform_body h2.gsection_title:first-of-type {
  margin: 15px 0 20px;
}
.gform_wrapper form .gform_body .gform_fields li.gfield {
  margin: 0 0 15px;
  padding: 0;
}
.gform_wrapper form .gform_body .gform_fields li.gfield:before {
  display: none;
}
.gform_wrapper form .gform_body li.gfield {
  width: 100%;
  position: relative;
  clear: both;
  margin: 0 0 15px;
}
.gform_wrapper form .gform_body li.gfield.gf_left_half,
.gform_wrapper form .gform_body li.gfield.gf_right_half {
  width: 100%;
  clear: none;
}
.gform_wrapper form .gform_body li.gfield.gf_left_third,
.gform_wrapper form .gform_body li.gfield.gf_middle_third,
.gform_wrapper form .gform_body li.gfield.gf_right_third {
  width: 100%;
  clear: none;
}
.gform_wrapper form .gform_body li.gfield.gfield_error input[type=text],
.gform_wrapper form .gform_body li.gfield.gfield_error input[type=email],
.gform_wrapper form .gform_body li.gfield.gfield_error input[type=password],
.gform_wrapper form .gform_body li.gfield.gfield_error textarea,
.gform_wrapper form .gform_body li.gfield.gfield_error select {
  border: none;
  border-bottom: 1px solid #A9233E;
}
.gform_wrapper form .gform_body li.gfield.gfield_error .validation_message {
  position: absolute;
  font-size: 14px;
  font-weight: bold;
  color: #A9233E;
}
.gform_wrapper form .gform_body li.gfield.is-textarea {
  margin-top: 20px;
}
.gform_wrapper form .gform_body li.gfield.is-textarea.label-up .gfield_label {
  top: -10px;
}
.gform_wrapper form .gform_body li.gfield.is-textarea .gfield_label {
  top: 25px;
  transform: none;
}
.gform_wrapper form .gform_body li.gfield.normal-label .gfield_label {
  position: relative;
  display: inline-block;
  font-size: 11px;
  top: auto;
  text-transform: uppercase;
  color: #656565;
  margin: 0 0 5px;
  transform: none;
  transition: none;
  font-weight: bold;
  left: 0;
}
.gform_wrapper form .gform_body li.gfield.label-up .gfield_label {
  top: 0;
  left: 0;
  font-size: 11px;
  text-transform: uppercase;
  color: #656565;
  font-weight: bold;
}
.gform_wrapper form .gform_body .gfield_label {
  position: absolute;
  font-size: 16px;
  color: #8B8B8B;
  top: calc(50% + 5px);
  left: 15px;
  transform: translateY(-50%);
  transition: 0.2s;
}
.gform_wrapper form .gform_body .gfield_label .gfield_required {
  color: #A9233E;
  padding: 0 2px;
