/*********************
        Header
**********************/
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #FFFFFF;
  z-index: 9999;
  padding: 5px 0 10px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  transition: 0.2s;
}
header.headroom--unpinned {
  transform: translateY(-112px);
}
header .wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
}
header .left,
header .right {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}
header .left {
  justify-content: space-between;
  margin: 0 0 10px;
}
header .right {
  margin: 0 -10px;
}
header .right a {
  display: inline-block;
  width: 50%;
  margin: 0 10px;
}
header .right a:first-of-type {
  margin-left: 0;
}
header .right a:last-of-type {
  margin-right: 0;
}
header .logo {
  display: block;
  min-width: 150px;
  padding-right: 10px;
}
header .menu-trigger {
  display: inline-block;
  position: absolute;
  right: 10px;
  width: 20px;
  height: 14px;
  margin-right: 10px;
}
header .menu-trigger > span {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background: #656565;
  transition: 0.1s 0.1s;
  transition-delay: 0s, 0.1s;
  transform: translateY(-50%);
}
header .menu-trigger:before,
header .menu-trigger:after {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #656565;
  transition-duration: 0.1s, 0.1s;
  transition-delay: 0.1s, 0s;
}
header .menu-trigger:before {
  top: 0;
  transition-property: top, transform;
}
header .menu-trigger:after {
  bottom: 0;
  transition-property: bottom, transform;
}
header nav.main-navigation {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
header nav.main-navigation > ul {
  position: absolute;
  top: 171px;
  left: 0;
  width: 100%;
  transform: translateY(-10px);
  opacity: 0;
  visibility: hidden;
  transition: 0.1s;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  background: #FFFFFF;
}
header nav.main-navigation > ul > li:last-of-type > a {
  border-bottom: none;
}
