  display: inline-block;
  width: 100%;
  position: relative;
  color: #656565;
  text-decoration: none;
  font-size: 14px;
  padding: 10px 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  font-family: 'Helvetica Neue','Helvetica',Arial,sans-serif;
  font-weight: 400;
  font-style: normal;
}
header nav.main-navigation > ul > li > a:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 5px;
  width: 0;
  height: 2px;
  background: #FFFFFF;
  transition: 0.2s;
}
header nav.main-navigation > ul > li:hover > a:after {
  width: 100%;
}
header nav.main-navigation > ul > li.has-dropdown .dropdown {
  display: none;
}
header.menu-open .menu-trigger > span {
  width: 0;
}
header.menu-open .menu-trigger:before {
  top: 6px;
  transform: rotate(45deg);
}
header.menu-open .menu-trigger:after {
  bottom: 6px;
  transform: rotate(-45deg);
}
header.menu-open nav.main-navigation > ul {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}
